
Template.__checkName("afSelectizeInput");
Template["afSelectizeInput"] = new Template("Template.afSelectizeInput", (function() {
  var view = this;
  return HTML.INPUT(HTML.Attrs({
    type: "text",
    value: function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("."), "value"));
    }
  }, function() {
    return Spacebars.attrMustache(view.lookup("atts"));
  }));
}));
